export default {
	data() {
		return {
			providerList: [],
			showPassword: true,
			showPassword1: true,
			account: '', //账号
			password: '', //密码
			conPassword: '', //确认密码
			vCode: '', //填写的验证码
			code: '', //获取的验证码
			isOk: false,
			n: 60,
			positionTop: 0,
			list:{}
		};
	},
	methods: {
		clear(i) {
			switch (i) {
				case 1:
					this.account = '';
					break;
				case 2:
					this.realName = '';
					break;
				case 3:
					this.weixin = '';
					break;
				case 4:
					this.tenxun = '';
					break;
				case 5:
					this.telephone = '';
					break;
				case 6:
					this.picture = '';
					break;
				case 7:
					this.unit = '';
					break;
				default:
					break;
			}
		},
		look(i) {
			switch (i) {
				case 1:
					this.showPassword = !this.showPassword;
					break;
				case 2:
					this.showPassword1 = !this.showPassword1;
					break;
			}
		},
		setUp() {
			var that = this;
			if (/^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/.test(that.account)) {
				that.n = 60;
				var time = setInterval(function() {
					that.isOk = true;
					that.n--;
					if (that.n < 0) {
						clearInterval(time);
						that.isOk = false;
					}
				}, 1000);
				var url = 'teacherUser/findSendCode.json';
				var data = {
					mobile: this.account
				};
				this.uniHttp.getJSON(
					url,
					data,
					res => {
						for (let i in res.header) {
							if (i == 'Set-Cookie') {
								this.config.cleanUserCookie();
								this.config.normalizeUserCookie(res.header[i]);
								uni.showToast({
									icon: 'none',
									title: res.data.msg
								});
							}
						}
					},
					err => {
						console.log(err);
					}
				);
			} else {
				uni.showToast({
					icon: 'none',
					title: '手机号码有误'
				});
				return;
			}
		},
		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		register() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			var mPattern = /^1[3456789]\d{9}$/;
			if (!mPattern.test(this.account)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号'
				});
				return;
			} 
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.password != this.conPassword) {
				uni.showToast({
					icon: 'none',
					title: '输入的俩次的密码不一致'
				});
				return;
			}
			/**
			 * 下面简单模拟下服务端的处理
			 * 检测用户账号密码是否在已注册的用户列表中
			 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
			 */
			const data = {
				account: this.account,
				password: this.password,
				vCode: this.vCode,
			};
			let url = 'teacherUser/findPassword.json';
			this.uniHttp.getJSON(
				url,
				data,
				res => {
					console.log(res);
					if (res.data.success) {
						uni.showToast({
							icon: 'none',
							title: "修改成功"
						});
						uni.setStorage({
							key:'userInfo',
							data:''
						})
						uni.navigateTo({
							url: '../login/login',
						});
					}else{
						uni.showToast({
							icon: 'none',
							title: "修改失败"
						});
					}
				},
				err => {
					uni.showToast({
						icon: 'none',
						title: "修改失败"
					});
				}
			);
		}
	}
};
